<template>
<!-- 
  getBargainSubgroupProductPage  
 -->
  <el-dialog :append-to-body="true" title="" :visible.sync="bargainCommodityShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!--  -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span></span>
        <el-button style="float: right" @click="getData" type="primary" size="small"></el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small"></el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="">
            <el-input style="width: 203px" v-model="searchList.activeName" clearable placeholder="" @input="change($event)"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container" shadow="never" style="margin-top:10px;">
      <el-table 
        ref="multipleTable" 
        :data="list" 
        tooltip-effect="dark" 
        style="width: 100%" 
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="" prop="productName" width="140">
          <template slot-scope="scope">
            <el-popover placement="right" title trigger="hover">
              <el-image
                slot="reference"
                :src="scope.row.imageUrl"
                :alt="scope.row.imageUrl"
                style="max-height: 40px;max-width: 40px"
              ></el-image>
              <el-image :src="scope.row.imageUrl" style="max-height: 350px;max-width: 350px"></el-image>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="" prop="activeName"></el-table-column>
        <el-table-column label="" prop="productName" width="300"></el-table-column>
        <el-table-column label="" prop="beginTime"></el-table-column>
        <el-table-column label="" prop="endTime"></el-table-column>
        <el-table-column label="" prop="endTime">
          <template slot-scope="scope">
            <div class="warningFont" v-if="scope.row.activityStatus == 0"></div>
            <div v-if="scope.row.activityStatus == 1"></div>
            <div class="dangerFont" v-if="scope.row.activityStatus == 2"></div>
            <div class="dangerFont" v-if="scope.row.activityStatus == 3"></div>
          </template>

        </el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close"> </el-button>
      <el-button type="primary" @click="sub"> </el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { getBargainSubgroupProductPage } from '@/api/marketManage/bargain'
export default {
  props: ["bargainIds","bargainCommodityShow"],
  watch:{
    bargainCommodityShow(newvalue,oldValue){
      if(newvalue){
        this.getData();
      }
    },
  },
  data(){
    return{
      fromLoading:false,
      searchList:{
        pageNum:1,
        pageSize:5
      },
      list:[],
      listTotal:0,
      multipleSelection:[],           // 
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
  },
  methods:{
    sub(){
      console.log('multipleSelection',this.multipleSelection)
      if(this.multipleSelection.length == 0){
        this.$message.error('');
        return;
      }
      this.$emit('bargainCommodityFun',this.multipleSelection)
      this.$refs.multipleTable.clearSelection();
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
    reset(){
      this.searchList.activeName = ''
      this.getData();
    },
    //  - 
    getData(){
      this.fromLoading = true
      this.searchList.bargainIds = this.bargainIds || ''
      console.log('',this.searchList)
      getBargainSubgroupProductPage(this.searchList).then(res=>{
        console.log('',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
        this.fromLoading = false
      })
    },
    close(){
      this.$emit('bargainCommodityClose',true)
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style>
  .row-expand-cover .el-table__expand-icon{visibility:hidden;}
</style>