<template>
<!-- 

  getShopActivityListById     
  discountId:''   id  
  

 -->
  <el-dialog :append-to-body="true" title="" :visible.sync="discountListShow" width="70%" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!--  -->
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span></span>
        <el-button style="float: right" @click="getData" type="primary" size="small"></el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small"></el-button>
        <el-button style="float: right;margin-right: 15px" @click="openPage" size="small"></el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="searchList.beginTime"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetime"
              placeholder=""
              align="right"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="searchList.endTime"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetime"
              placeholder=""
              align="right"
              :picker-options="pickerOptions">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container" shadow="never" style="margin-top:10px;">
      <el-table
        ref="multipleTable"
        :data="list"
        tooltip-effect="dark"
        style="width: 100%">
        <el-table-column label="" prop="name"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            {{scope.row.activityBeginTime + '  ' + scope.row.activityEndTime}}
          </template>
        </el-table-column>
        <el-table-column label="" width="140">
          <template slot-scope="scope">
            <div style="color:#E6A23C;" v-if="scope.row.activityStatus == 0"></div>
            <div style="color:#409EFF;" v-if="scope.row.activityStatus == 1"></div>
            <div style="color:#F56C6C;" v-if="scope.row.activityStatus == 2"></div>
            <div style="color:#F56C6C;" v-if="scope.row.activityStatus == 3"></div>
          </template>
        </el-table-column>
        <el-table-column prop="productTotal" label="" width="140"></el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
            <el-button @click="sub(scope.row)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close"> </el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { getShopActivityListById } from '@/api/marketManage/discount'
export default {
  props: ["discountId","discountListShow"],
  watch:{
    discountListShow(newvalue,oldValue){
      if(newvalue){
        console.log('')
        this.searchList.id = this.discountId || '';
        this.getData();
      }
    },
  },
  data(){
    return{
      fromLoading:false,
      searchList:{
        pageNum:1,
        pageSize:5
      },
      list:[],
      listTotal:0,
      pickerOptions: {
        shortcuts: [{
          text: '',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.searchList.activityType = 1
  },
  methods:{
    sub(e){
      console.log('e',e)
      this.$emit('discountFun',e)
    },
    openPage(){
      let details = this.$router.resolve({
        name: "",
      });
      window.open(details.href, "_blank")
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.searchList.pageSize = val;
      this.getData()
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.searchList.pageNum = val;
      this.getData()
    },
    reset(){
      this.searchList.name = ''
      this.searchList.beginTime = ''
      this.searchList.endTime = ''
      this.getData();
    },
    //  - 
    getData(){
      this.fromLoading = true
      console.log('',this.searchList)
      getShopActivityListById(this.searchList).then(res=>{
        console.log('',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
        this.fromLoading = false
      })
      
    },
    close(){
      this.$emit('discountClose',true)
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>