<template>
  <div class="itemBox">
    <p class="title"></p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="">
        <el-input v-model="itemData.title" maxlength="5" placeholder="5"></el-input>
      </el-form-item>
      <el-form-item label="" class="navigation">
        <div class="goodsUpload">+</div>
        <p class="prompt">,20</p>
      </el-form-item>
      <el-form-item label="" class="navigation">
        <el-radio-group v-model="itemData.listNumber">
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
        </el-radio-group>
        <p class="prompt">,,</p>
      </el-form-item>
      <el-form-item label="" class="navigation">
        <el-radio-group v-model="itemData.listType" @change="showType">
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
          <el-radio :label="3"></el-radio>
          <el-radio :label="4"></el-radio>
          <el-radio :label="5"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" class="checkboxGroup">
        <el-checkbox v-model="checked" disabled></el-checkbox>
        <el-checkbox v-model="checked" disabled></el-checkbox>
        <el-checkbox v-model="checked" disabled></el-checkbox>
        <el-checkbox v-model="itemData.lineation"></el-checkbox>
      </el-form-item>
    </el-form>

    <!-- {{itemData}} -->
  </div>
</template>

<script>
export default {
  props: {
    //
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      checked: true
    };
  },
  methods: {
    //
    showType() {
      switch (this.itemData.listType) {
        case 0:
          // 
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 1:
          // 
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 2:
          // 
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 3:
          // 
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle =
            "0px 2px 2px 0px rgba(153,153,153,0.1)";
          break;
        case 4:
          // 
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle =
            "0px 2px 2px 0px rgba(153,153,153,0.1)";
          break;
        case 5:
          // 
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
      }
    }
  }
};
</script>

