  border: 1px solid #000;
}
.title {
  border-bottom: 1px solid #eee;
  padding: 20px 18px 24px 18px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .checkboxGroup .el-form-item__content {
    margin-left: 10px;
  }
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .navigation {
    border-bottom: 1px solid #eee;
