  }
  .prompt {
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 20px;
    letter-spacing: 0px;
    margin-top: 5px;
  }
  .goodsUpload {
    font-size: 34px;
    background: #fcfcfc;
    font-weight: 100;
    color: #9a9a9a;
    width: 68px;
    height: 68px;
    border-radius: 4px 4px 4px 4px;
    text-align: center;
    line-height: 68px;
    border: 1px dashed #ddd;
    margin-bottom: 10px;
    transition: 0.2s;
