                <img :src="item.img" />
              </li>
            </draggable>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <draggable
              element="div"
              tag="ul"
              v-model="listLeft1"
              :options="dragOptions1"
              :move="onMove"
              @start="isDragging=true"
              @end="onEnd">
              <li v-for="(item,key) in listLeft1" :key="key">
                <img :src="item.img" />
              </li>
            </draggable>
          </el-tab-pane>
        </el-tabs>
      </div>
      <!---->
      <div class="list-center">
        <div class="phoneBox">
          <img class="battery" src="@/assets/decorateBox/battery.png" />
          <p class="leftPhoneBar">iPhone8</p>
          <draggable
            element="div"
            v-model="listRight"
            :options="dragOptions2"
            :move="onMove"
            @start="isDragging=true"
            @end="onEnd"
            class="draggable_content"
            :style="{background:listRight[0].backgroundColor}">
