    <pre>{{list2String}}</pre>-->
  </div>
</template>

<script>
/** 

1name,typefixednametypefixedfalse
   
2listRight
     
3listLeft
4 onEndselects
5Mixins 
6dragOptions2draggableclass
6clone
  vuedraggable/dist/vuedraggable.common.js clone

  es6var original = _.cloneDeep(obj);

  //
clone: {
    type: Function,
    default: function _default(obj) {
      var original = Array.isArray(obj) ? [] : {};
      for (var key in obj) {
        if (obj.hasOwnProperty(key)) {
          if (typeof obj[key] === 'object' && obj[key]!==null) {
            original[key] = deepCopy(obj[key]);   //
          } else {
            original[key] = obj[key];
          }
        }
      }
      return original;
    }
  },
 **/
import draggable from "vuedraggable";

