import perchBox from "./components/normal/perchBox"; //
import couponBox from "./components/normal/couponBox"; //
import discountBox from "./components/normal/discountBox"; //
import groupBox from "./components/normal/groupBox"; //
import bargainBox from "./components/normal/bargainBox"; //

