import editorNoticeBox from "./components/editor/editorNoticeBox"; //
import editorPerchBox from "./components/editor/editorPerchBox"; //
import editorCouponBox from "./components/editor/editorCouponBox"; //
import editorDiscountBox from "./components/editor/editorDiscountBox"; //
import editorGroupBox from "./components/editor/editorGroupBox"; //
import editorBargainBox from "./components/editor/editorBargainBox"; //

//
import {
  addShopVpage,
