  editShopVpage
} from "@/api/shop/shopDecoration";
export default {
  name: "drag",
  components: {
    draggable,
    //
    headerBox, //
    richTxtBox, //
    commodityBox, //
    bannerBox, //
    magicBox, //
    shopBox, //
    titleBox, //
    noticeBox, //
    perchBox, //
    couponBox, //
    discountBox, //
    groupBox, //
    bargainBox, //

    //
    editorheaderBox, //
    editorRichTxtBox, //
    editorCommodityBox, //
    editorBannerBox, //
    editorMagicBox, //
    editorShopBox, //
    editorTitleBox, //
    editorNoticeBox, //
    editorPerchBox, //
    editorCouponBox, //
    editorDiscountBox, //
    editorGroupBox, //
    editorBargainBox //
  },
  created() {
    this.vpageId = this.$route.query.vpageId;
    // 
    if (this.vpageId != undefined) {
      findShopVpageById(this.vpageId).then(res => {
        console.log('',res)
        if (res.code == 200) {
          let arr1 = [
            //
            {
              type: "headerBox",
              title: res.data.name, //
              describe: res.data.description, //
              backgroundColor: res.data.bgcolor, //
              bgSelect: res.data.bgcolor == "#fff" ? 0 : 1, //
              fixed: false
            }
          ];
          let arr2 = JSON.parse(res.data.content);
          let arr = [...arr1, ...arr2];
          this.listRight = arr;
          this.editorData = arr[0];
        }
      });
    }
  },
  mounted() {},
  data() {
    let listRights = [
      //
      {
        type: "headerBox",
        title: "", //
        describe: "", //
        backgroundColor: "#fff", //
        bgSelect: 0, //
        fixed: false
      }
    ];
    return {
      hackReset: true, //
      isDragging: false,
      activeName: "first", //
      loading: false, //
      btnName: "", //
      vpageId: "", //	ID
      //
      listLeft: [
        // 
        {
          name: "",
          type: "richTxtBox",
          backgroundColor: "#fff",
          html: "", //
          isFullScreen: 1, // 0 1
          img: require("@/assets/decorateBox/basis/basis1.png"),
          fixed: false //false
        },
        // 
        {
          name: "",
          type: "commodityBox",
          // 0 1 2 3 4 5
          listType: 0,

          // buyBtn=26  buyBtnName1
          // buyBtn=37  buyBtnName

          buyBtnName: "",
          buyBtnName1: "",
          // 
          //  0 1
          chamfering: 0,
          chamferingStyle: 0,
          //  0 1
          projection: 0,
          projectionStyle: "0px 0px 0px 0px",
          //  0 1
          fill: 0,
          fillStyle: 0,
          //
          // showContent: "0,1,2",
          //
          showGoodsName: true,
          //
          showGoodsPrice: true,
          //
          showBuyBtn: true,
          //
          showCornerMark: false,
          //
          buyBtn: 7, //
          // -1
          cornerMark: -1,
          showStyle: "10px",
          // 
          goodsData: [],
          commodityIds:[],
          //
          anglePicture: {},
          img: require("@/assets/decorateBox/basis/basis2.png"),
          fixed: false
        },
        // 
        {
          name: "",
          type: "bannerBox",
          // 0 1
          bannerType: 0,
          // 
          advertisingType: 0,
          //  0 1
          chamfering: 0,
          chamferingStyle: 0,
          //  0 1
          projection: 0,
          projectionStyle: "0px 0px 0px 0px",
          //  0 1
          fill: 1,
          fillStyle: "10px",
          //  02:1 143
          ratio: 0,
          ratioStyle: 0,
          //
          bannerBg: [],
          img: require("@/assets/decorateBox/basis/basis3.png"),
          fixed: false //false
        },
        // 
        {
          name: "",
          type: "magicBox",
          selectTemplate: 0, //
          magicBoxData1: [
            {
              urlInfo: "",
              goodIdOrUrl: "",
              title: "",
              status: ""
            }
          ], //
          magicBoxData2: [
            {
              urlInfo: "",
              goodIdOrUrl: "",
              title: "",
              status: ""
            }
          ], //
          magicBoxData3: [
            {
              urlInfo: "",
              goodIdOrUrl: "",
              title: "",
              status: ""
            }
          ], //
          magicBoxData4: [
            {
              urlInfo: "",
              goodIdOrUrl: "",
              title: "",
              status: ""
            }
          ], //
          clearance: 0, //
          img: require("@/assets/decorateBox/basis/basis4.png"),
          fixed: false
          // undefined
        },
        // 
        {
          name: "",
          type: "shopBox",
          switchShop: 0, //01
          shopsData: [], //
          img: require("@/assets/decorateBox/basis/basis5.png"),
          fixed: false //false
        },
        // 
        {
          name: "",
          type: "titleBox",
          title: "",
          // 
          fontSize: 32,
          //
          textSelect: 0,
          fontWeight: 100,
          // 
          showAddress: 0,
          showAlign: "left",
          //
          textColor: "#000",
          textColorSelect: 0,
          // 
          backgroundColor: "#fff",
          bgSelect: 0,
          img: require("@/assets/decorateBox/basis/basis6.png"),
          fixed: false
        },
        // 
        {
          name: "",
          type: "noticeBox",
          title: "",
          //
          textColor: "#fb3b06",
          textColorSelect: 0,
          // 
          backgroundColor: "#fbf5dd",
          bgSelect: 0,
          img: require("@/assets/decorateBox/basis/basis7.png"),
          fixed: false //false
        },
        // 
        {
          name: "",
          type: "perchBox",
          // px
          height: 40,
          backgroundColor: "#fff",
          img: require("@/assets/decorateBox/basis/basis8.png"),
          // 01
          bgSelect: 1,
          fixed: false
        }
      ], //,name,typenametype
      listLeft1: [
        // {
        //   name: "",
        //   type: "groupBox",
        //   //
        //   title: "",
        //   // 0 1
        //   listNumber: 0,
        //   // 0 1 2 3 4 5
        //   listType: 0,

        //   // 
        //   chamferingStyle: "8px",
        //   // 
        //   projectionStyle: "0px 0px 0px 0px",
        //   //  false true
        //   lineation: true,
        //   img: require("@/assets/decorateBox/marketing/marketing3.png"),
        //   fixed: false
        // },
        {
          name: "",
          type: "bargainBox",
          //
          title: "",
          // 
          showBox:[
            {
              productName:'2...',
              activityPrice:'688.00',
              minPrice:'999.99',
              imageUrl:''
            }
          ],
          // 0 1 2 3 4 5
          listType: 0,
          //  false true
          lineation: true,
          //  false true
          showMore: true,
          // 
          goodsData: [
            {
              productName:'2...',
              activityPrice:'688.00',
              minPrice:'999.99',
              imageUrl:'',
              showFalse:true,
              activityStatus:1
            }
          ],
          bargainIds:[],
          img: require("@/assets/decorateBox/marketing/marketing4.png"),
          fixed: false
        },
        {
          name: "",
          type: "discountBox",
          // 0 1 2 3 4 5
          listType: 0,
          // 
          titleAlign:0,
          // buyBtn=26  buyBtnName1
          // buyBtn=37  buyBtnName
          buyBtnName: "",
          buyBtnName1: "",
          // 
          //  0 1
          chamfering: 0,
          chamferingStyle: 0,
          //  0 1
          projection: 0,
          projectionStyle: "0px 0px 0px 0px",
          //  0 1
          fill: 0,
          fillStyle: 0,
          showBuyBtn: true,               //
          showLinePrice:true,             //
          showLast:true,                  //
          showTime:true,                  //
          showTimeLine:true,              //
          activityStatus:1,               // 
          timer:()=>{},
          timeObj:{
            d:'01',
            h:'20',
            m:'34',
            s:'56'
          },
          //
          buyBtn: 7, //
          showStyle: "10px",
          // 
          goodsData: [],
          discountCommodityIds:[],
          img: require("@/assets/decorateBox/marketing/marketing2.png"),
          fixed: false
        },
        {
          name: "",
          couponIds:[],
          coupons:[
            {
              discountAmount:'200',
              couponContext:'999',
              disabled:false
            },
            {
              discountAmount:'200',
              couponContext:'999',
              disabled:false
            },
            {
              discountAmount:'200',
              couponContext:'999',
              disabled:true
            }
          ],
          type: "couponBox",
          style:'style_1',  // 0 1 2 3
          color: "color_1",
          img: require("@/assets/decorateBox/marketing/marketing1.png"),
          fixed: false,
        },
      ], //,name,typenametype
      listRight: listRights,
      activeClass: -1, //class
      editorType: "editorheaderBox", //-
      editorData: listRights[0] //
    };
  },
  computed: {
    // 
    dragOptions1() {
      return {
        animation: 0,
        group: {
          name: "description", //name
          pull: "clone", //
          put: false
        },
        // ghostClass: "ghost",
        chosenClass: "ghost",
        sort: false //
      };
    },
    // 
    dragOptions2() {
      return {
        animation: 300, //
        group: "description", //name,
        // class
        draggable:
          ".richTxtBox,.commodityBox,.bannerBox,.magicBox,.shopBox,.titleBox,.noticeBox,.perchBox,.couponBox,.discountBox,.groupBox,.bargainBox" //clss
      };
    }

    //html
    // listString() {
    //   return JSON.stringify(this.listLeft, null, 2);
    // },
    // list2String() {
    //   return JSON.stringify(this.listRight, null, 2);
    // }
  },
  methods: {
    // 
    onMove({ relatedContext, draggedContext }) {
      const relatedElement = relatedContext.element;
      const draggedElement = draggedContext.element; //
      return (
        (!relatedElement || !relatedElement.fixed) && !draggedElement.fixed
      );
    },
    // 
    onEnd(evt) {
      this.isDragging = false;
      // console.log(evt.newIndex);
      let index = "";
      // console.log(this.listRight.length);
      //
      if (this.listRight.length < 3) {
        //
        index = evt.newIndex - 1;
      } else if (this.listRight.length == 1) {
        //
        index = 0;
      } else {
        //
        index = evt.newIndex;
      }
      this.activeClass = index; // 
      // console.log(evt)
      //
      //
      switch (this.listRight[index].type) {
        //case  
        //editorDp  -
        case "headerBox":
          this.editorType = "editorheaderBox";
          break;
        case "richTxtBox":
          this.editorType = "editorRichTxtBox";
          break;
        case "commodityBox":
          this.editorType = "editorCommodityBox";
          break;
        case "bannerBox":
          this.editorType = "editorBannerBox";
          break;
        case "magicBox":
          this.editorType = "editorMagicBox";
          break;
        case "shopBox":
          this.editorType = "editorShopBox";
          break;
        case "titleBox":
          this.editorType = "editorTitleBox";
          break;
        case "noticeBox":
          this.editorType = "editorNoticeBox";
          break;
        case "perchBox":
          this.editorType = "editorPerchBox";
          break;
        case "couponBox":
          this.editorType = "editorCouponBox";
          break;
        case "discountBox":
          this.editorType = "editorDiscountBox";
          break;
        case "groupBox":
          this.editorType = "editorGroupBox";
          break;
        case "bargainBox":
          this.editorType = "editorBargainBox";
          break;
        case undefined:
          this.editorType = "editorheaderBox";
          break;
      }
      //this.editorData = this.listRight[index]; //
      // console.log(index);
      this.selects(index, this.listRight[index]);
    },
    // 
    selects(index, item) {
      this.activeClass = index;
      // console.log(this.activeClass);
      //
      //
      switch (this.listRight[index].type) {
        //case  
        //editorDp  -
        case "headerBox":
          this.editorType = "editorheaderBox";
          break;
        case "richTxtBox":
          // 
          this.editorType = "editorRichTxtBox";
          this.hackReset = false;
          this.$nextTick(() => {
            this.hackReset = true;
          });
          break;
        case "commodityBox":
          this.editorType = "editorCommodityBox";
          break;
        case "bannerBox":
          this.editorType = "editorBannerBox";
          break;
        case "magicBox":
          this.editorType = "editorMagicBox";
          break;
        case "shopBox":
          this.editorType = "editorShopBox";
          break;
        case "titleBox":
          this.editorType = "editorTitleBox";
          break;
        case "noticeBox":
          this.editorType = "editorNoticeBox";
          break;
        case "perchBox":
          this.editorType = "editorPerchBox";
          break;
        case "couponBox":
          this.editorType = "editorCouponBox";
          break;
        case "discountBox":
          this.editorType = "editorDiscountBox";
          break;
        case "groupBox":
          this.editorType = "editorGroupBox";
          break;
        case "bargainBox":
          this.editorType = "editorBargainBox";
          break;
      }
      this.editorData = item; //
    },
    // - 
    deleteBox(box) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        let index = this.listRight.findIndex(item => item == box);
        if (index != -1) {
          this.listRight.splice(index, 1);
          // this.selects(index-1,this.listRight[index-1])
          if (box == this.editorData) {
            //
            this.selects(
              this.listRight.length - 1,
              this.listRight[this.listRight.length - 1]
            );
          } else {
            let index = this.listRight.findIndex(
              item => item == this.editorData
            );
            this.selects(index, this.editorData);
          }
        }
      }).catch(() => {});
    },
    // 
    getElementToPageTop(el) {
      if (el.parentElement) {
        return this.getElementToPageTop(el.parentElement) + el.offsetTop;
      }
      return el.offsetTop;
    },
    // 
    shelves() {
      //
      let data = this.listRight;
      for (let i = 0; i < data.length; i++) {
        console.log(data[i])
        switch (data[i].type) {
          case "headerBox":
            if (data[i].title == "") {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight + 60;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "richTxtBox":
            if (data[i].html == "") {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 200;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "commodityBox":
            if (data[i].goodsData.length == 0) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "bannerBox":
            if (data[i].bannerBg.length == 0) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "magicBox":
            let selectTemplate = data[i].selectTemplate;
            let pds1 = data[i].magicBoxData1[0];
            let pds2 = data[i].magicBoxData2[0];
            let pds3 = data[i].magicBoxData3[0];
            let pds4 = data[i].magicBoxData4[0];

            let arr1 = Object.keys(pds1);
            let arr2 = Object.keys(pds2);
            let arr3 = Object.keys(pds3);
            let arr4 = Object.keys(pds4);

            let pd1 = arr1.length;
            let pd2 = arr2.length;
            let pd3 = arr3.length;
            let pd4 = arr4.length;

            function pd() {
              if (pd1 == 4 || pd2 == 4) {
                return false;
              } else if (selectTemplate == 1) {
                if (pd3 == 4) {
                  return false;
                }
              } else if (selectTemplate == 2) {
                if (pd3 == 4 || pd4 == 4) {
                  return false;
                }
              } else if (selectTemplate == 3) {
                if (pd3 == 4 || pd4 == 4) {
                  return false;
                }
              } else if (selectTemplate == 4) {
                if (pd3 == 4) {
                  return false;
                }
              } else if (selectTemplate == 5) {
                if (pd3 == 4) {
                  return false;
                }
              } else if (selectTemplate == 6) {
                if (pd3 == 4 || pd4 == 4) {
                  return false;
                }
              }
            }
            if (pd() == false) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }

            break;
          case "shopBox":
            console.log('shopBox',data[i]);
            if (data[i].shopsData.length == 0) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "titleBox":
            if (data[i].title == "") {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight + 60;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "noticeBox":
            if (data[i].title == "") {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight + 100;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "couponBox":
            if (data[i].couponIds.length == 0) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "discountBox":
            if (!data[i].activityId) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            if (data[i].goodsData.length == 0) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight - 300;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
          case "groupBox":
            break;
          case "bargainBox":
            // console.log(data[i]);
            if (!data[i].title) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            if (data[i].goodsData.length == 0 || data[i].goodsData[0].showFalse) {
              this.$message.error("");
              // 
              this.selects(i, data[i]);
              // 
              this.$nextTick(() => {
                let id = `ids${i}`;
                let oant = document.getElementById(id);
                let sp = oant.scrollTop + oant.offsetHeight;
                let getPoint = obj => {
                  let t = obj.offsetTop;
                  while ((obj = obj.offsetParent)) {
                    t += obj.offsetTop;
                  }
                  return t;
                };
                // console.log(sp);
                window.scroll(0, getPoint(oant) - sp);
              });
              return;
            }
            break;
        }
      }
      if (this.vpageId == undefined) {
        this.submit();
      } else {
        this.editorSubmit();
      }
    },
    //
    submit() {
      var arr = this.listRight;
      var content = arr.concat();
      content.shift();
      console.log(content);
      if (content.length != 0) {
        this.loading = true;
        this.btnName = "...";
        let params = {
          shopId: Number(localStorage.getItem("shopId")),
          name: this.listRight[0].title,
          description: this.listRight[0].describe,
          bgcolor: this.listRight[0].backgroundColor,
          qrcodeUrl: "",
          isIndex: 1, //0-1-
          sample: "",
          content: content
        };
        addShopVpage(params).then(res => {
          if (res.code == 200) {
            this.$message({
              message: "",
              type: "success"
            });
            this.btnName = "";
            setTimeout(()=>{
              this.loading = false;
              this.$router.push({path:'/shop/shopHome'})
            },1000)
            
          } else {
            this.$message.error("");
            this.loading = false;
            this.btnName = "";
          }
        });
      }
    },
    //
    editorSubmit() {
      var arr = this.listRight;
      var content = arr.concat();
      console.log(content)
      content.shift();
      if (content.length != 0) {
        this.loading = true;
        this.btnName = "...";
        let params = {
          shopId: Number(localStorage.getItem("shopId")),
          name: this.listRight[0].title,
          description: this.listRight[0].describe,
          bgcolor: this.listRight[0].backgroundColor,
          qrcodeUrl: "",
          isIndex: 1, //0-1-
          sample: "",
          content: content,
          vpageId: this.vpageId
        };
        editShopVpage(params).then(res => {
          if (res.code == 200) {
            this.$message({
              message: "",
              type: "success"
            });
            this.btnName = "";
            setTimeout(()=>{
              this.loading = false;
              this.$router.push({path:'/shop/shopHome'})
            },1000)
          } else {
            this.$message.error("");
            this.loading = false;
            this.btnName = "";
          }
        });
      }
    }
  }
};
</script>

