<template>
  <div class="itemBox">
    <p class="title">
      &nbsp;
      <span></span>
    </p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="" class="navigation">
        <div class="goodsUploadListBtn">
          <ol class="goodsUploadList" v-show="itemData.shopsData.length!=0">
            <li
              v-for="(item,index) in itemData.shopsData"
              :key="index"
              @mouseover="mouseOver(index)"
              @mouseleave="mouseLeave"
            >
              <img :src="item.roomPicture" alt />
              <img
                class="delBtn"
                :class="hoverIndex==index?'delBtnShow':''"
                src="@/assets/decorateBox/goods/close.png"
                @click="delGoods(index)"
                alt
              />
            </li>
          </ol>
          <div class="goodsUpload" @click="addShops" v-show="itemData.shopsData.length<3">+</div>
        </div>

        <p class="prompt">
          3
          
          
          
        </p>
      </el-form-item>
      <el-form-item label="" class="navigation">
        <el-radio-group v-model="itemData.switchShop">
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
        </el-radio-group>
        <ul>
          <li>
            <p></p>
            <p>- </p>
            <p>- </p>
            <p></p>
            <p></p>
          </li>
          <li>
            <p></p>
            <p>-</p>
            <p></p>
            <p></p>
            <p>-</p>
          </li>
        </ul>
      </el-form-item>
      <el-form-item class="expore">
        <el-button type="primary" plain></el-button>
      </el-form-item>
    </el-form>
    <!-- {{itemData}} -->
    <!--  -->

    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="76%"
      :close-on-click-modal="false"
      @closed="closed"
      @close="submitFormqx"
      :append-to-body="true"
      class="add-dialog" top="6vh" >
      <el-form class="search" ref="search1" :model="search1" label-width="76px" size="mini">
        <el-form-item label=":" prop="name">
          <el-input v-model="search1.name" placeholder=""></el-input>
        </el-form-item>
        <div class="formBtnBox">
          <el-button @click="resetForm('search1')" size="mini"></el-button>
          <el-button type="primary" icon="el-icon-search" @click="searchSubmit" size="mini"></el-button>
        </div>
      </el-form>
      <el-table
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%"
        stripe
        height="380"
        v-loading="loading"
        @selection-change="handleSelectionChange"
        :header-cell-style="{background:'#f5f7fa',color:'#606266'}"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="" width="70" align="center" type="index">
          <template slot-scope="scope">{{scope.$index+(nowPage - 1) * pageNumber + 1}}</template>
        </el-table-column>

        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-popover placement="right" title trigger="hover">
              <el-image slot="reference" :src="scope.row.roomPicture" :alt="scope.row.roomPicture" style="max-height: 40px;max-width: 40px"></el-image>
              <el-image :src="scope.row.roomPicture" style="max-height: 350px;max-width: 350px"></el-image>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="" show-overflow-tooltip align="center">
          <template slot-scope="scope">{{ scope.row.roomName}}</template>
        </el-table-column>
        <el-table-column label="" show-overflow-tooltip align="center">
          <template slot-scope="scope">{{ scope.row.provinceName}}{{ scope.row.cityName}}{{ scope.row.areaName}}{{ scope.row.locationDesc}}</template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="nowPage"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="pageNumber"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        ></el-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm()" :loading="loadingbut">{{loadingbuttext}}</el-button>
        <el-button @click="submitFormqx()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { selectShopRoomList } from "@/api/shop/shopDecoration";
import { mapState } from 'vuex'
export default {
  props: {
    //
    itemData: {
      type: Object,
      default:{}
    },
  },
  watch:{
    itemData(newvalue,oldValue){
      if(newvalue){
        console.log('')
      }
    },
    immediate:true,
    deep:true
  },
  created(){
    console.log('editShopBoxitemData',this.itemData)
    this.$set(this.itemData)
    console.log(this.getData)
  },
  data() {
    return {
      hoverIndex: -1, //hoverindex
      loadingbut: false, //
      loadingbuttext: " ",
      pageNumber: 5, //
      nowPage: 1, //
      total: 1,
      tableData: [],
      search: {
        name: ""
      },
      search1: {
        name: ""
      },
      multipleSelection: [], //
      loading: false,
      dialogVisible: false
    };
  },
  methods: {
    //
    submitFormqx() {
      this.dialogVisible = false;
      this.$refs.search1.resetFields();
      this.search.name = "";
    },

    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    submitForm() {
      let pd = Number(this.multipleSelection.length) + Number(this.itemData.shopsData.length);
      if (this.multipleSelection.length != 0 && pd <= 3) {
        this.loadingbut = true;
        this.loadingbuttext = "...";
        this.dialogVisible = false;
        this.multipleSelection.map(item => {
          this.itemData.shopsData.push(item);
        });
      } else if (pd > 3) {
        this.$message.error("	");
      } else {
        this.$message.error("");
      }
    },
    // 
    required(pageNo, pageSize, ids) {
      let params = {
        pageNum: pageNo,
        pageSize: pageSize,
        shopId: this.shop.shopId,
        roomName: this.search.name,
        ids: ids //
      };
      this.loading = true;
      selectShopRoomList(params).then(res => {
        if (res.code == 200) {
          this.tableData = res.data.list;
          this.total = res.data.total;
          this.loading = false;
        }
      });
    },
    // 
    addShops() {
      this.dialogVisible = true;
      let id = [];
      this.itemData.shopsData.map(item => {
        id.push(item.id);
      });
      let ids = id.join(",");
      console.log(ids);
      console.log(this.itemData.shopsData);
      this.required(this.nowPage, this.pageNumber, ids);
    },
    //  - 
    handleCurrentChange(val) {
      this.nowPage = val;
      this.required(val, this.pageNumber);
    },
    //  -
    handleSizeChange(val) {
      this.pageNumber = val;
      this.required(1, val);
    },
    closed() {
      this.loadingbut = false;
      this.loadingbuttext = " ";
    },

    //
    resetForm(formName) {
      this.$nextTick(() => {
        this.$refs[formName].resetFields();
        this.search.name = "";
        this.searchSubmit()
      });
    },
    //  - 
    searchSubmit() {
      this.search.name = this.search1.name;
      this.required(1, 5);
    },
    // 
    mouseOver(index) {
      this.hoverIndex = index;
    },
    // 
    mouseLeave() {
      this.hoverIndex = -1;
    },
    // 
    delGoods(index) {
      this.itemData.shopsData.splice(index, 1);
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ]),
    getData(){
      console.log('')
      return this.itemData
    }
  },
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  border-bottom: 1px solid #eee;
  padding: 20px 18px 24px 18px;
  color: #333;
  font-size: 16px;
  font-weight: 700;
  span {
    height: 14px;
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 14px;
    letter-spacing: 0px;
    font-weight: 100;
  }
}
.el-dialog__body {
  .pagination {
    padding-top: 20px;
  }
  .search {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .el-form-item {
      margin-bottom: 0px;
      margin-right: 20px;
    }
    .formBtnBox {
      margin-left: 20px;
    }
  }
  img {
    width: 100px;
    margin: 0 auto;
  }
}
.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .expore {
    text-align: right;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 16px;
    .el-radio {
      margin-left: 10px;
      width: 60px;
      margin-bottom: 10px;
    }
    ul {
      background-color: #fcfcfc;
      padding: 12px 10px;
      margin-top: 3px;
      li {
        p {
          font-size: 12px;
          color: rgba(102, 102, 102, 1);
          line-height: 20px;
          letter-spacing: 0px;
        }
      }
      li:first-child {
        margin-bottom: 10px;
      }
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
  .el-checkbox-group {
    margin-left: 10px;
  }
  .el-checkbox {
    display: block;
    margin: 0;
  }
  .prompt {
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 20px;
    letter-spacing: 0px;
    margin-top: 5px;
  }
  .goodsUploadListBtn{
    display: flex;
  }
  .goodsUploadList {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 68px;
      height: 68px;
      transition: 0.2s;
      cursor: pointer;
      border: 1px solid #ddd;
      box-sizing: border-box;
      padding: 4px;
      position: relative;
      margin-right: 10px;
      margin-bottom: 10px;
      img {
        width: 100%;
        height: 100%;
      }
      .delBtn {
        width: 16px;
        height: 16px;
        position: absolute;
        right: -8px;
        top: -8px;
        display: none;
      }
      .delBtnShow {
        display: block;
      }
    }
    li:hover {
      border: 1px solid #6babec;
    }
  }
  .goodsUpload {
    font-size: 34px;
    background: #fcfcfc;
    font-weight: 100;
    color: #9a9a9a;
    width: 68px;
    height: 68px;
    border-radius: 4px 4px 4px 4px;
    text-align: center;
    line-height: 68px;
    border: 1px dashed #ddd;
    margin-bottom: 10px;
    transition: 0.2s;
    cursor: pointer;
  }
  .goodsUpload:hover {
    background-color: #ecf5ff;
    border: 1px dashed rgba(0, 121, 253, 1);
  }
}
</style>
