<script>
import imageMaterial from "@/components/shopMaterial/imageMaterial";
import singleCommodity from '@/components/popover/singleCommodity';
import singleVpage from '@/components/popover/singleVpage';
export default {
  props: {
    //
    itemData: {
      type: Object
    }
  },
  created() {
    this.shopId = Number(localStorage.getItem("shopId"));
  },
  data() {
    return {
      magicBoxSelect: 0,

      dialogVisible: false,
      shopId: "",
      getPost: true,

      // 
      loading: false,
      loadingbut: false, //
      loadingbuttext: " ",
      pageNumber: 5, //
      nowPage: 1, //
      total: 1,
      tableData: [],
      selectData: [],
      search: {
        name: "",
        shopCategoryId: ""
      },
      multipleSelection: [], //

      // 

      pageNumber2: 10, //
      nowPage2: 1, //
      total2: 1,
      tableData2: [],
      search2_1: {
        name: ""
      },
      search2: {
        name: ""
      },
      dialogVisible2: false,
      multipleSelection2: [], //
      // 
      commodityShow:false,      
      // 
      vpageShow:false,
    };
  },
  components: {
    imageMaterial,
    singleCommodity,
    singleVpage
  },
  methods: {
    //  
    vpageClose(){
      this.vpageShow = false;
    },
    vpageFun(e){
      console.log('',e)
      if (this.magicBoxSelect == 0) {
        this.itemData.magicBoxData1[0].goodIdOrUrl = e[0].vpageId;
        this.itemData.magicBoxData1[0].urlInfo = e[0].vpageName;
        this.itemData.magicBoxData1[0].status = 1;
      }
      if (this.magicBoxSelect == 1) {
        this.itemData.magicBoxData2[0].goodIdOrUrl = e[0].vpageId;
        this.itemData.magicBoxData2[0].urlInfo = e[0].vpageName;
        this.itemData.magicBoxData2[0].status = 1;
      }
      if (this.magicBoxSelect == 2) {
        this.itemData.magicBoxData3[0].goodIdOrUrl = e[0].vpageId;
        this.itemData.magicBoxData3[0].urlInfo = e[0].vpageName;
        this.itemData.magicBoxData3[0].status = 1;
      }
      if (this.magicBoxSelect == 3) {
        this.itemData.magicBoxData4[0].goodIdOrUrl = e[0].vpageId;
        this.itemData.magicBoxData4[0].urlInfo = e[0].vpageName;
        this.itemData.magicBoxData4[0].status = 1;
      }
      this.vpageShow = false;
    },
    //  
    addBanner(e, index) {
      this.getPost = true;
      this.dialogVisible = true;
    },
    commodityClose(){
      this.commodityShow = false;
    },
    commodityFun(e){
      if (this.magicBoxSelect == 0) {
        this.itemData.magicBoxData1[0].goodIdOrUrl = e[0].productId;
        this.itemData.magicBoxData1[0].urlInfo = e[0].productName;
        this.itemData.magicBoxData1[0].status = 0;
      }
      if (this.magicBoxSelect == 1) {
        this.itemData.magicBoxData2[0].goodIdOrUrl = e[0].productId;
        this.itemData.magicBoxData2[0].urlInfo = e[0].productName;
        this.itemData.magicBoxData2[0].status = 0;
      }
      if (this.magicBoxSelect == 2) {
        this.itemData.magicBoxData3[0].goodIdOrUrl = e[0].productId;
        this.itemData.magicBoxData3[0].urlInfo = e[0].productName;
        this.itemData.magicBoxData3[0].status = 0;
      }
      if (this.magicBoxSelect == 3) {
        this.itemData.magicBoxData4[0].goodIdOrUrl = e[0].productId;
        this.itemData.magicBoxData4[0].urlInfo = e[0].productName;
        this.itemData.magicBoxData4[0].status = 0;
      }
      this.commodityShow = false
    },
    // 
    pname1() {
      let name = "";
      let box1 = this.itemData.magicBoxData1[0].status;
      if (box1 == 0) {
        name = " | ";
      } else if (box1 == 1) {
        name = " | ";
      } else {
        name = "";
      }
      return name;
    },
    pname2() {
      let name = "";
      let box2 = this.itemData.magicBoxData2[0].status;
      if (box2 == 0) {
        name = " | ";
      } else if (box2 == 1) {
        name = " | ";
      } else {
        name = "";
      }
      return name;
    },
    pname3() {
      let name = "";
      let box3 = this.itemData.magicBoxData3[0].status;
      if (box3 == 0) {
        name = " | ";
      } else if (box3 == 1) {
        name = " | ";
      } else {
        name = "";
      }
      return name;
    },
    pname4() {
      let name = "";
      let box4 = this.itemData.magicBoxData4[0].status;
      if (box4 == 0) {
        name = " | ";
      } else if (box4 == 1) {
        name = " | ";
      } else {
        name = "";
      }
      return name;
    },
    // 
    select(e) {
      this.itemData.selectTemplate = e;
      this.magicBoxSelect = 0;
    },
    //
    submitFormqx() {
      this.dialogVisible = false;
    },

    //
    submitForm(e) {
      console.log('e',e)
      let obj = e[0];
      this.dialogVisible = false;
      console.log('this.magicBoxSelect',this.magicBoxSelect)
      switch (this.magicBoxSelect) {
        case 0:
          console.log('Jinlai',this.itemData.magicBoxData1)
          obj.goodIdOrUrl = this.itemData.magicBoxData1[0].goodIdOrUrl || ''
          obj.urlInfo = this.itemData.magicBoxData1[0].urlInfo || ''
          obj.status = this.itemData.magicBoxData1[0].status || ''
          obj.title = this.itemData.magicBoxData1[0].title || ''
          this.itemData.magicBoxData1.splice(0,1,obj)
          console.log('this.itemData.magicBoxData1',this.itemData.magicBoxData1)
          break;

        case 1:
          obj.goodIdOrUrl = this.itemData.magicBoxData2[0].goodIdOrUrl || ''
          obj.urlInfo = this.itemData.magicBoxData2[0].urlInfo || ''
          obj.status = this.itemData.magicBoxData2[0].status || ''
          obj.title = this.itemData.magicBoxData2[0].title || ''
          this.itemData.magicBoxData2.splice(0,1,obj)
          break;

        case 2:
          obj.goodIdOrUrl = this.itemData.magicBoxData3[0].goodIdOrUrl || ''
          obj.urlInfo = this.itemData.magicBoxData3[0].urlInfo || ''
          obj.status = this.itemData.magicBoxData3[0].status || ''
          obj.title = this.itemData.magicBoxData3[0].title || ''
          this.itemData.magicBoxData3.splice(0,1,obj)
          break;

        case 3:
          obj.goodIdOrUrl = this.itemData.magicBoxData4[0].goodIdOrUrl || ''
          obj.urlInfo = this.itemData.magicBoxData4[0].urlInfo || ''
          obj.status = this.itemData.magicBoxData4[0].status || ''
          obj.title = this.itemData.magicBoxData4[0].title || ''
          this.itemData.magicBoxData4.splice(0,1,obj)
          break;
      }
      this.getPost = false;
    },
    //
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    closed() {
      this.loadingbut = false;
      this.loadingbuttext = " ";
    },
    //
    pd() {
      if (
        this.magicBoxSelect == 0 &&
        this.itemData.magicBoxData1[0].id == undefined
      ) {
        return false;
      }
      if (
        this.magicBoxSelect == 1 &&
        this.itemData.magicBoxData2[0].id == undefined
      ) {
        return false;
      }
      if (
        this.magicBoxSelect == 2 &&
        this.itemData.magicBoxData3[0].id == undefined
      ) {
        return false;
      }
      if (
        this.magicBoxSelect == 3 &&
        this.itemData.magicBoxData4[0].id == undefined
      ) {
        return false;
      }
    },
    selsectStatus() {
      if (this.pd() == false) {
        this.$message.error("");
        return;
      }
      this.commodityShow = true;
    },
    // 
    microPage() {
      if (this.pd() == false) {
        this.$message.error("");
        return;
      }
      this.vpageShow = true;
    },
