<template>
  <div class="itemBox">
    <p class="title"></p>
    <el-form ref="form" :model="itemData" size="mini">
      <el-form-item label="">
        <el-input v-model="itemData.title" maxlength="10" show-word-limit></el-input>
      </el-form-item>
      <el-form-item label="" class="navigation">
        <div class="goodsUpload" @click="bargainCommodityShow = true">+</div>
        <ul class="goodsUploadList" v-show="itemData.goodsData.length!=0">
          <li
            v-for="(item,index) in itemData.goodsData"
            :key="index"
            @mouseover="mouseOver(index)"
            @mouseleave="mouseLeave"
            v-show="!item.showFalse">
            <img :src="item.imageUrl" alt />
            <img
              class="delBtn"
              :class="hoverIndex==index?'delBtnShow':''"
              src="@/assets/decorateBox/goods/close.png"
              @click="delGoods(index)"
              alt
            />
          </li>
        </ul>
      </el-form-item>
      <el-form-item label="" class="navigation">
        <el-radio-group v-model="itemData.listType" @change="showType">
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
          <el-radio :label="2"></el-radio>
          <el-radio :label="3"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="" class="checkboxGroup">
        <el-checkbox v-model="checked" disabled></el-checkbox>
        <el-checkbox v-model="checked" disabled></el-checkbox>
        <el-checkbox v-model="checked" disabled></el-checkbox>
        <el-checkbox v-model="itemData.lineation"></el-checkbox>
        <el-checkbox v-model="itemData.showMore"></el-checkbox>
        <p></p>
      </el-form-item>
    </el-form>
    <!--  -->
    <bargainCommodity 
      :bargainCommodityShow="bargainCommodityShow" 
      :bargainIds="itemData.bargainIds.join(',')" 
      @bargainCommodityClose="bargainCommodityClose" 
      @bargainCommodityFun="bargainCommodityFun"></bargainCommodity>
  </div>
</template>

<script>
import bargainCommodity from '@/components/popover/bargainCommodity';

export default {
  props: {
    //
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      bargainCommodityShow:false,     //
      checked: true,
      hoverIndex: -1, //hoverindex
      loadingbut: false, //
      loadingbuttext: " ",
      loading: false,
      bargainIds:''
    };
  },
  methods: {
    // 
    bargainCommodityClose(){
      this.bargainCommodityShow = false;
    },
    bargainCommodityFun(e){
      console.log('',e)
      if(this.itemData.goodsData[0].showFalse) this.itemData.goodsData = []
      e.forEach( item => {
        this.itemData.goodsData.push(item)
        this.itemData.bargainIds.push(item.bargainId)
      })
      console.log('',this.itemData.goodsData)
      this.bargainCommodityShow = false;
    },
    //
    showType() {
      switch (this.itemData.listType) {
        case 0:
          // 
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 1:
          // 
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle =
            "0px 2px 2px 0px rgba(153,153,153,0.1)";
          break;
        case 2:
          // 
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 3:
          // 
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle =
            "0px 2px 2px 0px rgba(153,153,153,0.1)";
          break;
        case 4:
          // 
          this.itemData.chamferingStyle = 0;
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
        case 5:
          // 
          this.itemData.chamferingStyle = "8px";
          this.itemData.projectionStyle = "0px 0px 0px 0px";
          break;
      }
    },
    // 
    mouseOver(index) {
      this.hoverIndex = index;
    },
    // 
    mouseLeave() {
      this.hoverIndex = -1;
    },
    // 
    delGoods(index) {
      this.itemData.goodsData.splice(index, 1);
      this.itemData.bargainIds.splice(index, 1);
      if(this.itemData.goodsData.length == 0){
        this.itemData.goodsData.push({
          productName:'2...',
          activityPrice:'688.00',
          minPrice:'999.99',
          imageUrl:'',
          showFalse:true,
          activityStatus:1
        })
      }
    }
  },
  components:{
    bargainCommodity
  }
};
</script>

<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  padding: 20px 18px 24px 18px;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.el-dialog__body {
  .pagination {
    padding-top: 20px;
  }
  .search {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .el-form-item {
      margin-bottom: 0px;
      margin-right: 20px;
    }
    .formBtnBox {
      margin-left: 20px;
    }
  }
  img {
    width: 100px;
    margin: 0 auto;
  }
}

.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .checkboxGroup .el-form-item__content {
    margin-left: 10px;
  }
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    color: #333;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 16px;
    .el-radio {
      margin-left: 10px;
      width: 64px;
      margin-bottom: 10px;
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
  .el-checkbox-group {
    margin-left: 10px;
  }
  .el-checkbox {
    display: block;
    margin: 0;
  }
  .prompt {
    font-size: 12px;
    color: rgba(153, 153, 153, 1);
    line-height: 20px;
    letter-spacing: 0px;
    margin-top: 5px;
  }
  .goodsUploadList {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 68px;
      height: 68px;
      transition: 0.2s;
      cursor: pointer;
      border: 1px solid #ddd;
      box-sizing: border-box;
      padding: 4px;
      position: relative;
      margin-right: 5px;
      margin-bottom: 10px;
      img {
        width: 100%;
        height: 100%;
      }
      .delBtn {
        width: 16px;
        height: 16px;
        position: absolute;
        right: -8px;
        top: -8px;
        display: none;
      }
      .delBtnShow {
        display: block;
      }
    }
    li:hover {
      border: 1px solid #6babec;
    }
  }
  .goodsUpload {
    font-size: 34px;
    background: #fcfcfc;
    font-weight: 100;
    color: #9a9a9a;
    width: 68px;
    height: 68px;
    border-radius: 4px 4px 4px 4px;
    text-align: center;
    line-height: 68px;
    border: 1px dashed #ddd;
    margin-bottom: 10px;
    transition: 0.2s;
    cursor: pointer;
  }
  .goodsUpload:hover {
    background-color: #ecf5ff;
    border: 1px dashed rgba(0, 121, 253, 1);
  }
}
</style>
